<div class="user-info"><div class="left-info"><div class="head-image"><img :src="img"><div class="name">{{ form.name }}</div><div class="code">id: {{ form.code }}</div></div><div class="tabs"><div class="tab-item" :class="{ active: title === &quot;我的资料&quot; }" @click="title = &quot;我的资料&quot;">我的资料</div><div class="tab-item" :class="{ active: title === &quot;修改密码&quot; }" @click="title = &quot;修改密码&quot;">修改密码</div></div></div><div class="right-info"><div class="title">{{ title }}</div><div class="user-container" v-if="title === &quot;我的资料&quot;"><a-form :model="form" :labelCol="{ span: 2 }" :wrapperCol="{ span: 4 }"><a-form-item label="头像"><img :src="img" style="width: 80px"></a-form-item><a-form-item label="账号"><div>{{ form.code }}</div></a-form-item><a-form-item label="班级"><div>{{ form.code }}</div></a-form-item><a-form-item label="姓名"><a-input v-model:value="form.name"></a-input></a-form-item><a-form-item label="个人简介" :wrapperCol="{ span: 14 }"><a-textarea v-model:value="form.personalSignature" :maxLength="500"></a-textarea></a-form-item><div class="buttons" style="padding-left: 8%"><a-button type="primary" @click="ok">确定</a-button><a-button style="margin-left: 12px" @click="back">取消</a-button></div></a-form></div><div class="user-container" v-if="title === &quot;修改密码&quot;"><a-form :model="formPassWord" :labelCol="{ span: 2 }" :wrapperCol="{ span: 4 }"><a-form-item label="原密码"><a-input-password class="password" v-model:value="formPassWord.oldPassword" placeholder="请输入现在使用的密码"></a-input-password></a-form-item><a-form-item label="新密码"><a-input-password class="password" v-model:value="formPassWord.newPassword" placeholder="8位数以上的新密码"></a-input-password></a-form-item><a-form-item label="确认密码"><a-input-password class="password" v-model:value="password" placeholder="请再次输入新密码"></a-input-password></a-form-item><div class="buttons" style="padding-left: 8%"><a-button type="primary" @click="passWordOk">确定</a-button><a-button style="margin-left: 12px" @click="back">取消</a-button></div></a-form></div></div></div>