
import {
  reactive, ref, onMounted,
} from 'vue';
import {
  Form, Input, Button, message,
} from 'ant-design-vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default {
  components: {
    aForm: Form,
    aFormItem: Form.Item,
    aInput: Input,
    aTextarea: Input.TextArea,
    aInputPassword: Input.Password,
    aButton: Button,
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    const form = reactive({
      code: '',
      name: '',
      personalSignature: '',
    });
    const img = ref('');

    const title = ref('我的资料');

    const formPassWord = reactive({
      code: '',
      oldPassword: '',
      newPassword: '',
    });
    const password = ref('');
    // 更改个人信息
    function ok() {
      store.dispatch('userInfo/changeUserSignature', form).then(() => {
        store.dispatch('user/getByCode');
      });
    }
    // 改密码
    function passWordOk() {
      return new Promise((resolve, reject) => {
        if (formPassWord.newPassword !== password.value) {
          message.info('两次密码输入不一致！');
          return;
        }
        if (formPassWord.newPassword.length < 8) {
          message.info('密码长度过短！');
          return;
        }
        store
          .dispatch('userInfo/changePassword', formPassWord)
          .then((res) => {
            formPassWord.newPassword = '';
            formPassWord.oldPassword = '';
            password.value = '';
            resolve(res);
          })
          .catch((err) => {
            reject(err);
          });
      });
    }

    // 返回
    function back() {
      router.go(-1);
    }
    onMounted(() => {
      store.dispatch('user/getByCode').then((res) => {
        formPassWord.code = res.code;
        form.code = res.code;
        form.name = res.name;
        form.personalSignature = res.personalSignature;
        img.value = res.headImagePath;
      });
    });
    return {
      title,
      form,
      formPassWord,
      password,
      ok,
      back,
      passWordOk,
      img,
    };
  },
};
